import { Table, Button, Container } from "react-bootstrap";

export default function TabelaVisitantesSelecionados(props){

    
    return(
        <Container className="mt-5 mb-5">
        <Table striped bordered hover>
            <thead>
                <tr>
                <th>Código</th>
                <th>Nome</th>
                <th>Sobrenome</th>
                <th>CPF</th>
                <th>RG</th>
                <th>Telefone</th>
                <th>Data de cadastro</th>
                <th>Categoria</th>
                <th>Observações</th>
                <th></th>
                </tr>
            </thead>
            <tbody>
            {
                props.listaVisitantes?.map((visitante) => {
                    return <tr key={visitante.codigo}>
                                <td>{visitante.codigo}</td>
                                <td>{visitante.nome}</td>
                                <td>{visitante.sobrenome}</td>
                                <td>{visitante.cpf}</td>
                                <td>{visitante.rg}</td>
                                <td>{visitante.telefone}</td>
                                <td>{visitante.dataCadastro}</td>
                                <td>{visitante.codCategoria.descricao}</td>
                                <td>{visitante.observacao}</td>
                                <td>
                                    <Button variant="danger" onClick={()=>{
                                        const lista = props.listaVisitantes.filter((vis) => vis.codigo !== visitante.codigo);
                                        props.setAgendamento({...props.dadosAgendamento, listaVisitantes:lista});
                                        props.setListaVisitantes(lista);
                                    }} className="ms-2"> 
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash3" viewBox="0 0 16 16">
                                            <path d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z"/>
                                        </svg>
                                    </Button>
                                </td>
                            </tr>
                })
            }
            </tbody>
            </Table>
        </Container>
  );
}