export const AjudaVisitantes = 
    <p>Nessa página você terá acesso a lista de todos os visitantes cadastrados!<br/><br />
    A cima da tabela possui um filtro onde você pode pesquisar pelo nome e/ou sobrenome do visitante.<br/><br />
    As ações incluem a edição (botão com lápis), na qual pode se editar os dados do visitante, e exclusão (botão com lixeira), na qual irá excluir o visitante.<br/><br />
    No final da página possui o botão Relatório que permiti imprimir ou salvar como pdf a lista de visitantes cadastrados.<br/><br />
    E por fim o botão Cadastrar que irá direcionar o usuário ao formulário de cadastro de um novo visitante.</p>


